import React, { Component } from 'react'
import {
    Route,
    BrowserRouter as Router,
    Link,
    Redirect,
    Switch
} from 'react-router-dom'

import { firebaseAuth } from '../data/config'

import Home from './pages/'
import About from './pages/About'
import DashboardCourses from './pages/protected/'
import Register from './pages/Register'
import Login from './pages/Login'
import Error404 from './pages/Error404'
import {logout} from './helpers/Auth'

import 'pure-css/lib/menus.css'
import './index.css'
import phoenix from './media/phoenix.png'

const PrivateRoute = ({component:Component, authed, rest})=>(
    <Route 
        {...rest} 
            render={                 
            props => authed === true
                ? <Component {...props}/>
                : <Redirect to={{pathname: '/login', state:{from:props.location}}}/>
    }/>
)

const PublicRoute = ({component:Component, authed, rest})=>(
    <Route 
    {...rest} 
        render={                 
        props => authed === false
            ? <Component {...props}/>
            : <Redirect to='/cursos' />
}/>
)

class App extends Component {

    constructor(...props) {
        super(...props)

        this.state = {
            //Si cambia true , muestra cursos y logout
            authed: false,
            loading: true
        }

        this.handleonClick = this.handleonClick.bind(this)
    }

    handleonClick(e) {
        //alert('click')
        document.getElementById('tuckedMenu').classList.toggle('custom-menu-tucked');
        document.getElementById('toggle').classList.toggle('x');
    }

    //Capturar la validación de Firebase
    componentDidMount(){
        //evalua en todo momento la autenticación. Proporcioando por Firebase
        this.removeListener = firebaseAuth().onAuthStateChanged(user =>{
            if (user){
                this.setState({
                    authed:true,
                    loading:false
                })
            }else{
                this.setState({                    
                    loading:false
                })
            }
        })
    }

 //para asegurar que la sesion ya no exista
     componentWillUnmount(){
         this.removeListener()
     }

    //JSX
    //Código Javascript entre {}
    render() {
        return this.state.loading === true
            ? <h1>Cargando...</h1>
            : (
                <Router>
                    <div>
                        <header className="custom-menu-wrapper">
                            <div className="pure-menu custom-menu custom-menu-top">
                                <a href="#" className="pure-menu-heading custom-menu-brand">
                                    <img className="img-logo" src={phoenix} alt="React" />
                                </a>
                                <a href="#" className="custom-menu-toggle" id="toggle" onClick={this.handleonClick}><s className="bar"></s><s className="bar"></s></a>
                            </div>
                            <div className="pure-menu pure-menu-horizontal pure-menu-scrollable custom-menu custom-menu-bottom custom-menu-tucked" id="tuckedMenu">
                                <div className="custom-menu-screen"></div>
                                <ul className="pure-menu-list">
                                    <li className="pure-menu-item">
                                        <Link to="/" className="pure-menu-link" onClick={this.handleonClick}>Home</Link>
                                    </li>
                                    <li className="pure-menu-item">
                                        <Link to="/acerca" className="pure-menu-link" onClick={this.handleonClick}>Acerca</Link>
                                    </li>
                                    {
                                        (this.state.authed)
                                        ?
                                        <span>
                                            <li className="pure-menu-item">
                                                <Link to="/cursos" className="pure-menu-link" onClick={this.handleonClick}>Cursos</Link>
                                            </li>
                                            <li className="pure-menu-item">
                                                <Link to="/login" className="pure-menu-link" onClick={()=>{
                                                                                                            logout()
                                                                                                            this.setState({authed:false})
                                                                                                            this.handleonClick()}}>Logout</Link>
                                            </li>
                                        </span>
                                        :
                                        <span>
                                            <li className="pure-menu-item">
                                                <Link to="/registro" className="pure-menu-link" onClick={this.handleonClick}>Registro</Link>
                                            </li>
                                            <li className="pure-menu-item">
                                                <Link to="/login" className="pure-menu-link" onClick={this.handleonClick}>Login</Link>
                                            </li>
                                        </span>
                                    }                                    
                                </ul>
                            </div>
                        </header>
                        <main className="Main">
                            {/* Rutas protegidas siempre con switch */}
                            <Switch>
                                <Route path="/" exact component={Home} />
                                <Route path="/acerca" component={About} />
                                <PublicRoute authed={this.state.authed} path="/login" component={Login} />
                                <PublicRoute authed={this.state.authed} path="/registro" component={Register} />
                                <PrivateRoute authed={this.state.authed} path="/cursos" component={DashboardCourses} />
                                {/* Si hay otra ruta muestra el error 404 */}
                                <Route component={Error404} />
                            </Switch>
                        </main>
                    </div>
                </Router>
            )
    }
}

export default App