// Ayuda la gestion de autenticacion con Firebase

import { firebaseAuth, ref } from '../../data/config'

const saveUser = (user) => (
    ref
        .child(`users/${user.uid}/info`)
        .set({
            email: user.email,
            uid: user.uid
        })
        .then(() => user)
)

const auth = (email, password) => (
    firebaseAuth()
        .createUserWithEmailAndPassword(email, password)
        .then(saveUser)
)

const login = (email, password) => firebaseAuth().signInWithEmailAndPassword(email, password)

const logout = () => firebaseAuth().signOut()

const resetPassword = email => firebaseAuth().sendPasswordResetEmail(email)

//Archivo auxiliar para interactuar con firebase
//Se pueden exportar varios archivos
//Son funcionalidades que ayudan a interactuar con la integración de firebase
export {
    saveUser,
    auth,
    login,
    logout,
    resetPassword
}